@use "global/var" as v;
@use "global/mixins" as m;

.PlayeraContent {
  h2 {
    text-align: center;
  }
}
.PlayeraGrid {
  @include m.desktop {
    display: grid;
    grid-template-columns: 1fr 2fr 1fr;
    place-items: center center;
  }
}

.Playera {
  .PlayeraImagenes {
    display: flex;
    justify-content: space-between;
    @include m.desktop {
      flex-direction: column;
    }
    img {
      margin-bottom: 1rem;
      width: 10rem;
      height: 5rem;
      @include m.desktop {
        width: 20rem;
        height: 20rem;
      }
    }
  }

  .PlayeraTexto {
    border-top: 1px solid v.$White;
    text-align: center;
    padding: 1rem;
    @include m.desktop {
      height: 100%;
      border-left: 1px solid v.$White;
    }
  }
}
