@use "global/var" as v;
@use "global/mixins" as m;
.DarkModeAndNav {
  margin-top: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  @include m.desktop {
    flex-direction: row;
  }
}
.dark-mode {
  width: 5rem;
  filter: invert(100%);
}

.Dark__Mode--Active {
  h1,
  h2,
  h3,
  h4 {
    color: v.$White;
  }
  background-color: v.$DarkBg;
  color: v.$White;
  .footer {
    background-color: v.$Soft-Gray;
  }
  .Lamparas {
    .LamparaContenedor {
      .Lampara {
        .LamparaTexto {
          background-color: #333;
        }
      }
    }
  }

  .Mision__And__Blog {
    .Articulos {
      .Articulo {
        color: v.$White;
      }
    }
  }

  .SeccionBlogs {
    .Articulos {
      a {
        color: v.$White;
      }
    }
  }

  .meta-dato2 {
    color: v.$GreenWhite;
  }
}
