@use "global/var" as v;
@use "global/mixins" as m;

.Nav__Footer {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  margin-top: 4rem;
  padding: 1rem;
  h1 {
    color: v.$White;
  }
  nav {
    display: flex;
    flex-direction: column;
    gap: 2rem;
    @include m.desktop {
      flex-direction: row;
    }
    a {
      color: v.$White;
      &:hover {
        color: v.$GreenWhite;
      }
    }
  }
}

.footer {
  background-color: #333;
}
