@use "global/var" as v;
@use "global/mixins" as m;
.header {
  width: 100%;
  height: 70rem; // Altura fija como querías
  overflow: hidden;
  position: relative;
  .overley {
    z-index: 10;
    position: absolute;
    background: rgb(0, 0, 0);
    background: rgb(0, 0, 0);
    background: linear-gradient(
      90deg,
      rgba(0, 0, 0, 0.75) 0%,
      rgba(13, 13, 13, 0.6) 100%
    );
    justify-content: center;
    width: 100%;
    height: 100%;
  }
  .header--texto {
    z-index: 10;
    display: flex;

    flex-direction: column;
    justify-content: space-between;
    padding: 4rem;
    height: 100%;
    width: 100%;

    h1,
    h2,
    nav a {
      color: v.$White;
    }
    nav a {
      &:hover {
        color: v.$GreenWhite;
      }
    }
  }

  video {
    width: 100%;
    height: 100%; // Añadimos también
    object-fit: cover; // Para que el video no se deforme
    display: block;
  }
  .header--navegacion {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2rem;
    width: 100%;
    justify-content: space-between;
    @include m.desktop {
      flex-direction: row;
    }
    nav {
      display: flex;
      flex-direction: column;
      align-items: center;
      @include m.desktop {
        flex-direction: row;
        gap: 2rem;
      }
    }
  }
}

.headerNoneVideo {
  background-color: #333;
  padding: 2rem;
  height: 100%;
  width: 100%;
  h1 {
    color: v.$White;
  }
}

.header__NoneVideo-nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  @include m.desktop {
    flex-direction: row;
    justify-content: space-between;
  }

  nav {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2rem;
    @include m.desktop {
      flex-direction: row;
    }
    a {
      color: v.$White;
      &:hover {
        color: v.$GreenWhite;
      }
    }
  }
}
